#home{
  position: absolute;
  overflow: hidden;
  top: 0px;
  bottom: 0px;
  width: 100%;
  font-family: 'Fredericka the Great', cursive;
}
.full {
  height: 100%;
  display: grid;
  grid-template-rows: repeat (5, 1fr);
  grid-template-columns: auto-fit 1fr 1fr 1fr 600px;
  grid-gap: 15px;
  padding: 20px 0 20px 20px;
}
.full > a,
.iconsnav:not(.welcome) {
  /* border-left: 4px black solid;
  border-top: 4px black solid;*/
  align-self:center;
  text-align: center;
  color: deeppink;
  font-size:50px;
  margin:10px;
}
.full > a:hover{
  color: gold;
  transition: color 2s;
}
.home {
  grid-area: 1 / 1 / 4 / 1;
}
.work {
  grid-area: 2 / 2 / 4 / 4;
}
.welcome {
  grid-area: 1 / 4 / 5 / 6;
  text-align: right;
  background-image: url(../images/final824-roses-drawing.svg);
  background-size: auto;
  background-position: left;
  display: grid;
  place-items: center;
  border-left: none !important;
  text-shadow: 2px 2px black;
}
.welcome p {
  font-size: 3em;
  text-align: center;
  padding:5%;
}
.welcome p:hover{
  color: aquamarine;
  transition: color 2s;
}
.iconsnav {
  border: none;
  grid-area: 4 / 1 / 4 / 4;
  display:block;
}
.iconsnav a{
  padding: 0 10px 0 10px;
}
.iconsnav img{
  height: 1.5em;
  width: 1.5em;
}
.iconsnav a:last-child{
  font-weight:400;
  color:#FFD700;
  font-size: 1.5em;
}
.iconsnav a:last-child:hover{
  color: deeppink;
}
.iconsnav img:hover {
  filter: invert(39%) sepia(90%) saturate(7374%) hue-rotate(317deg) brightness(105%) contrast(101%);
}
