@import url(https://fonts.googleapis.com/css?family=Josefin+Slab|Fredericka+the+Great|Indie+Flower|Caveat|Lobster|Quicksand|Amatic+SC&display=swap);
#home{
  position: absolute;
  overflow: hidden;
  top: 0px;
  bottom: 0px;
  width: 100%;
  font-family: 'Fredericka the Great', cursive;
}
.full {
  height: 100%;
  display: grid;
  grid-template-rows: repeat (5, 1fr);
  grid-template-columns: auto-fit 1fr 1fr 1fr 600px;
  grid-gap: 15px;
  padding: 20px 0 20px 20px;
}
.full > a,
.iconsnav:not(.welcome) {
  /* border-left: 4px black solid;
  border-top: 4px black solid;*/
  align-self:center;
  text-align: center;
  color: deeppink;
  font-size:50px;
  margin:10px;
}
.full > a:hover{
  color: gold;
  -webkit-transition: color 2s;
  transition: color 2s;
}
.home {
  grid-area: 1 / 1 / 4 / 1;
}
.work {
  grid-area: 2 / 2 / 4 / 4;
}
.welcome {
  grid-area: 1 / 4 / 5 / 6;
  text-align: right;
  background-image: url(/portfolio/static/media/final824-roses-drawing.9c0c16ad.svg);
  background-size: auto;
  background-position: left;
  display: grid;
  place-items: center;
  border-left: none !important;
  text-shadow: 2px 2px black;
}
.welcome p {
  font-size: 3em;
  text-align: center;
  padding:5%;
}
.welcome p:hover{
  color: aquamarine;
  -webkit-transition: color 2s;
  transition: color 2s;
}
.iconsnav {
  border: none;
  grid-area: 4 / 1 / 4 / 4;
  display:block;
}
.iconsnav a{
  padding: 0 10px 0 10px;
}
.iconsnav img{
  height: 1.5em;
  width: 1.5em;
}
.iconsnav a:last-child{
  font-weight:400;
  color:#FFD700;
  font-size: 1.5em;
}
.iconsnav a:last-child:hover{
  color: deeppink;
}
.iconsnav img:hover {
  -webkit-filter: invert(39%) sepia(90%) saturate(7374%) hue-rotate(317deg) brightness(105%) contrast(101%);
          filter: invert(39%) sepia(90%) saturate(7374%) hue-rotate(317deg) brightness(105%) contrast(101%);
}

.about {
  padding: 50px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}
.about p {
  /* font-family: "Quicksand", sans-serif; */
  /* border-top: 1px solid gold;
  border-left: 1px solid gold; */
  font-size:1.5em;
}
.flex-row>p:nth-of-type(-n + 2) {
  padding: 50px 50px 50px 50px;
  border: 1px solid;
  margin-top: 50px;
  width: 100%;
}
.date p {
  /* background-color: gold; */
  width: 80px;
  height: 80px;
  line-height: 80px;
  border: 4px gold solid;
  border-radius: 50%;
  font-size: 3em;
  margin: 0 50px 50px 0;
}
.date {
  /* font-family: "Lobster", cursive; */
  padding: 15px 30px 15px 30px;
  shape-outside: circle(50%);
}
.skills i {
  margin: 10px;
  font-size:3.5em;
}
.skills>p:nth-of-type(-n+2) span{
  color:gold;
  word-break:break-word;
}
.flex-row>p{
  font-family: 'Fredericka the Great', cursive;
}
.projects {
  padding: 70px 50px 50px 50px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
  font-family: "Quicksand", sans-serif !important;
}

h2 {
  font-size: 166%;
  padding-bottom: 15px;
  font-family: "Lobster", cursive;
  color: gold;
}
.imagecenter {
  padding: 15px;
  text-align: center;
}
.projects img {
  padding-top: 70px;
  max-width: 100%;
  max-height: 100%;
}
.projects ul {
  margin-left: 15px;
  padding: 15px 15px 0;
  /* border-top: 1px solid gold;
  border-left: 1px solid gold; */
}
.projects ul:nth-of-type(2) {
  margin-bottom: 0;
}
.projects li {
  padding-bottom: 5px;
}
.projects li:nth-child(6) {
  font-size: 1.5em;
  padding-top: 15px;
  border-bottom: 1px solid gold;
}
.projects i {
  padding: 15px;
}
.projects span {
  padding-left: 2%;
}
.button:hover > span,
i {
  color: gold;
  border: gold;
}
.contact {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
  font-family: "Quicksand", sans-serif;
  padding: 50px;
}
form {
  margin-left: 15px;
}
input,
textarea,button {
  width: 40vw;
  border: 2px solid transparent;
  margin: 5px;
  padding: 5px;
  background-color: transparent;
  color: gold;
  font-size: 1.3em;
  font-family: "Quicksand", sans-serif;
}
textarea {
  height: 20vw;
}
.contact ul li:nth-child(1n + 1) {
  border-bottom: 1px solid gold;
}
.avatar {
  align-self: center;
  margin: 0 auto;
  background-image: url(/portfolio/static/media/myAvatar.97a8dc08.svg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 21vw;
  width: 21vw;
  border-radius: 50%;
  border: 3px solid white;
  text-align: center;
  margin-bottom: 25px;
}
h4 {
  display: inline-block;
  color: gold;
  font-size: 2em;
  position: relative;
  top: 101%;
  text-shadow: 2px 2px 2px black;
  font-family: "Amatic SC", cursive;
  line-height: 50px;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input::-moz-placeholder,
textarea::-moz-placeholder {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input::-ms-input-placeholder,
textarea::-ms-input-placeholder {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
input::placeholder,
textarea::placeholder {
  color: white;
  font-weight: bold;
  font-family: "Quicksand", sans-serif;
  text-align: left;
}
button[type="submit"]{
  text-align:left;

}
button[type="submit"] i{
  font-size:2em;
  color:white;
}
button[type="submit"] i:hover {
  color: gold;
  -webkit-transition: color 1s;
  transition: color 1s;
}
.collab{
  align-self:center;
  font-size:4em;
  width:50%;
  text-align:center;
  font-family: 'Fredericka the Great', cursive;
}
#next-line-intent{
  text-indent: 2em; 
}
#next-line-intent span{
  color:gold; 
}
.goback {
    font-size: 2em;
    position:relative;
  }
  .navstyle {
    padding: 50px 30px 30px 30px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    text-align: center;
  }
  .back-arrow::before{
    font-family: "Amatic SC", cursive;
    position: absolute;
    content:"<";
    color: gold;
    font-size: 3em;
    right: 25px;
    top: -60px;
  }
  .navstyle i {
    color: grey;
    font-size: 2em;
    padding:5px;
  }
  .navstyle a {
    padding: 0 3% 0 3%;
  }
  .navstyle i:hover {
    color: gold;
    -webkit-transition: color 1s;
    transition: color 1s;
  }
  .right img{
    height:3em;
    width: 3em;
    margin: 5%;
  }
@media screen and (max-width: 768px) {
  body {
    -ms-scroll-chaining: chained;
        overscroll-behavior: auto;
  }
  .responsive {
    height:100%;
    width: 100%;
    display: inline-grid;
    grid-template-columns: auto auto;
    grid-template-rows:auto auto;
  align-items: center;
  }
  .resFooter,.resWelcome,.iconsnav{
    border: 1px solid gold;
    padding: 35px;
    height: 100%;
  }
  h1, .resFooter {
    font-size: 3em;
  }
  .resFooter{
      grid-area: 1/1/1/1;
      align-self:center;
    }
    .resFooter li{
      padding: 20% 0 20% 0;
    }
    .resFooter i {
      color: gold;
      text-shadow: 2px 2px 2px grey;
    }
    .resWelcome {
      grid-area: 1/2/1/2;
      font-size: 8vh;
      text-align: end;
      text-shadow: 1px 1px black;
      font-family: "Amatic SC", cursive;
    }
    .iconsnav{
      grid-area: 2/1/2/3;
      line-height: 3em;

    }
    .about,
    .projects {
      padding: 0 0 30px 0;
    }
    .projects li:nth-child(6) {
      font-size: 2em;
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      padding: 30px;
    }
    .projects li a {
      padding: 15px;
      border: 2px solid white;
      border-radius: 50%;
      width: 80px;
      height: 80px;
      display: grid;
      place-content: center;
    }
    ul span {
      display: none;
    }
  }
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
text-rendering: optimizeLegibility !important;
-webkit-font-smoothing: antialiased !important;

}
*:before,
*:after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/portfolio/static/media/background.d891b24c.jpg) no-repeat center center fixed;
}
/* border page */
#top, #bottom, #left, #right {
  background: whitesmoke;
  position: fixed;
  z-index:1;
	}
	#left, #right {
    top: 0; bottom: 0;
		width: 5px;
  }
  #left { left: 0; }
  #right { right: 0; }
  
	#top, #bottom {
    left: 0; right: 0;
		height: 5px;
  }
  #top { top: 0; }
  #bottom { bottom: 0; }
  /* border page end*/
  
  .projects,
  .about,
  .contact {
    margin: 0 50px 70px 50px;
    background-color: rgba(0, 0, 0, 0.4);
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
  }
  .center{
    text-align:center;
  }
  .divider{
    width: 111px;
    display: inline-block;
    border: none;
    border-bottom: 5px solid gold;
  }
  h1 {
    font-size: 4em;
  color: white;
  text-align:center;
  font-weight:100;
  font-family: 'Fredericka the Great', cursive;
}
h3,
p,
i,
span {
  color: white;
}

/* animation */

.puff-in-center{-webkit-animation:puff-in-center .7s cubic-bezier(.47,0.000,.745,.715) both;animation:puff-in-center .7s cubic-bezier(.47,0.000,.745,.715) both}

/* keyframes */

@-webkit-keyframes puff-in-center{0%{-webkit-transform:scale(2);transform:scale(2);-webkit-filter:blur(4px);filter:blur(4px);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);-webkit-filter:blur(0);filter:blur(0);opacity:1}}

@keyframes puff-in-center{0%{-webkit-transform:scale(2);transform:scale(2);-webkit-filter:blur(4px);filter:blur(4px);opacity:0}100%{-webkit-transform:scale(1);transform:scale(1);-webkit-filter:blur(0);filter:blur(0);opacity:1}}

.App-header {
  font-size: calc(5px + 2vmin);
}


