.goback {
    font-size: 2em;
    position:relative;
  }
  .navstyle {
    padding: 50px 30px 30px 30px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    text-align: center;
  }
  .back-arrow::before{
    font-family: "Amatic SC", cursive;
    position: absolute;
    content:"<";
    color: gold;
    font-size: 3em;
    right: 25px;
    top: -60px;
  }
  .navstyle i {
    color: grey;
    font-size: 2em;
    padding:5px;
  }
  .navstyle a {
    padding: 0 3% 0 3%;
  }
  .navstyle i:hover {
    color: gold;
    transition: color 1s;
  }
  .right img{
    height:3em;
    width: 3em;
    margin: 5%;
  }